import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

export interface Rol {
  idRol: number;
  nombre: string;
  descripcion: string;
}

@Injectable({
  providedIn: 'root'
})
export class RolService {
  private apiUrl = '/api/roles';

  constructor(private http: HttpClient) {}

  listarRoles(): Observable<Rol[]> {
    return this.http.get<Rol[]>(this.apiUrl); // ‚úÖ Ya no pongas headers aqu√≠
  }

  crearRol(rol: Rol): Observable<Rol> {
    return this.http.post<Rol>(this.apiUrl, rol); // ‚úÖ El interceptor har√° su trabajo
  }

  eliminarRol(idRol: number): Observable<any> {
    return this.http.delete(`${this.apiUrl}/${idRol}`);
  }
}
